import { uniNumberBox } from "@dcloudio/uni-ui";
export default {
  components: {
    uniNumberBox
  },
  data() {
    return {
      modalName: null,
      listTouchStart: 0,
      listTouchDirection: null,
      options: [
        {
          text: "删除",
          style: {
            backgroundColor: "#FFB825"
          }
        }
      ],
      cartList: [
        {
          url: "/static/images/27891160-1_l_2.png",
          name: "中国少年儿童百科全书(全套共全套共全套共...",
          price: "96.72",
          no: 2,
          checked: 0,
          options: [{
            text: '删除',
            style: {
              backgroundColor: '#FFB825'
            }
          }]
        }
      ]
    };
  },
  methods: {
    onClick(e) {
      console.log('当前点击的是第' + e.index + '个按钮，点击内容是' + e.content.text)
    },
    change(open) {
      console.log('当前开启状态：' + open)
    },
    //加载购物车
    api_302() {
      let that = this;
      this.post(app_g.api.api_302, api.getSign(), function(vue, res) {
        if (res.data.Basis.State == app_g.state.state_200) {
          that.setData({
            result: res.data.Result
          });
          that.checkUpdate();
        } else {
          wx.showToast({
            title: res.data.Basis.Msg,
            icon: "none",
            duration: 3000
          });
        }
      });
    },
    //更新购物车
    api_303(item, num, cb) {
      let that = this;
      api.post(
        api.api_303,
        api.getSign({
          CID: item.id,
          Count: num,
          StoProductID: item.sto_product_id,
          SpecSet: item.specset,
          StoreID: that.data.order.store_id
        }),
        (wx, res) => {
          cb();
        }
      );
    },
    //删除购物车
    api_304(e) {
      let that = this;
      //数据
      let item = e.currentTarget.dataset.item;
      //请求接口删除
      api.post(
        api.api_304,
        api.getSign({
          CID: item.id
        }),
        function(wx, res) {
          if (res.data.Basis.State == api.state.state_200) {
            that.data.result.forEach((ele, index) => {
              if (ele.id === item.id) {
                that.data.result.shift(index, 1);
              }
            });

            that.setData({
              ["result"]: that.data.result
            });

            that.checkUpdate();
            wx.showToast({
              title: "删除成功",
              icon: "none",
              duration: 3000
            });
          } else {
            wx.showToast({
              title: res.data.Basis.Msg,
              icon: "none",
              duration: 3000
            });
          }
        }
      );
    },
    // ListTouch触摸开始
    ListTouchStart(e) {
      this.listTouchStart = e.touches[0].pageX;
    },

    // ListTouch计算方向
    ListTouchMove(e) {
      this.listTouchDirection =
        e.touches[0].pageX - this.listTouchStart > 0 ? "right" : "left";
    },

    // ListTouch计算滚动
    ListTouchEnd(e) {
      //console.log(this.listTouchDirection,e.currentTarget.dataset.target)
      if (this.listTouchDirection == "left") {
        console.log("aaa", e.currentTarget.dataset.target);
        this.modalName = e.currentTarget.dataset.target;
      } else {
        this.modalName = null;
      }
      this.listTouchDirection = null;
    }
  }
};
